<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <table border="0" class="cTable">
          <tr class="table-line1">
            <th>用户id</th>
            <th>名字</th>
            <th>余额</th>
            <th>操作</th>
          </tr>
          <tr v-for="(v,k) in list">
            <td>{{v.id}}</td>
            <td>{{v.nickname}} <i class="icon-note icons font-2xl d-block mt-4" @click="editShop(v.id,'nickname')"></i></td>
            <td>{{v.restMoney}} <i class="icon-note icons font-2xl d-block mt-4" @click="editShop(v.id,'restMoney')"></i></td>
            <td>
              暂无操作
            </td>
          </tr>
        </table>
      </b-col>
    </b-row>

  </div>

</template>

<script>
    import normalBtn from './normalBtn.vue';

    export default {
        name: 'tables',
        components: {normalBtn},
        data: () => {
            return {
                list: []
            }
        },

        methods: {
            changeValue(key,value){
                this[key] = value;
            },

            listShop() {
                this.$ajax('user', {
                    method: 'listAllUser',
                    token: window.localStorage.getItem('token')
                }, (res) => {
                    console.log(res);
                    if (res.code == 0) {
                        this.list = [];
                        for (var i in res.data) {
                            this.list.push(res.data[i]);
                        }
                    } else {
                        alert(res.msg);
                    }
                });
            },

            editShop(id, key) {
                var value = prompt('请输入新的' + (function (key) {
                    switch (key) {
                        case 'name':
                            return '名字';
                        case 'restMoney':
                            return '余额';
                    }
                })(key));

                if (!value) {
                    return;
                }

                if (confirm('确定要修改吗？')) {
                    this.$ajax('user', {
                        method: 'setUserInfo',
                        token: window.localStorage.getItem('token'),
                        key: key,
                        value: value,
                        userid:id
                    }, (res) => {
                        if (res.code == 0) {
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    });
                }
            }
        },

        created() {
            this.listShop();
        }
    }
</script>
<style type="text/css">
  .cTable {
    width: 100%;
  }

  .cTable * {
    text-align: center;
  }

  .table-line1 {
    border-bottom: solid 2px black;
  }

  .td-btn {
    margin: 10px 10px;
  }

  .menu-btn {
    margin: 10px 0 20px 0;
  }

  .icons {
    display: inline !important;
    font-size: 12px !important;
    margin: 0 5px;
    color: blue;
  }

  .displayAddShopIcon {
    color: white;
  }
</style>